import uniListItem from "@/components/uni-ui/uni-list-item/uni-list-item.vue"
import price       from "@/components/common/price.vue"
import { mapGetters } from "vuex"
export default {
	components:{ uniListItem,price },
	data() {
		return {
			path:false
		}
	},
	computed:{
		...mapGetters(['defaultPath'])
	},
	methods: {
		// 优惠券页面
		goCoupon(){
			uni.navigateTo({
				url:"../order-coupon/order-coupon"
			})
		},
		// 点击去付款，打开付款方式页面
		openPayMethods(){
			uni.navigateTo({
				url:"/pages/pay-methods/pay-methods"
			})
		},
		// 打开地址列表页
		openPathList(){
			uni.navigateTo({
				url:"/pages/user-path-list/user-path-list?type=choose"
			})
		},
		// 打开发票页
		openOrderInvoice(){
			uni.navigateTo({
				url:"/pages/order-invoice/order-invoice"
			})
		}
	},
	onLoad() {
		// 获取默认收货地址 注意通过filter得到的结果defaultPath是个数组
		if(this.defaultPath.length){
			this.path = this.defaultPath[0]
		}
		
		// 开启监听选择地址事件
		uni.$on('choosePath', res=>{
			this.path = res
		})
	},
	onUnload(){
		// 关闭监听收货地址
		uni.$off('choosePath', res=>{
			console.log('关闭监听收货地址', res);
		})
	}
}
